/* =====================================
   STYLES POUR LA SECTION CONTACT
   Design moderne avec formulaire et cartes
   ===================================== */

.contact-section {
  padding: var(--space-5xl) 0;
  background: linear-gradient(135deg, 
    rgba(100, 181, 246, 0.1) 0%, 
    rgba(138, 132, 226, 0.1) 50%,
    rgba(76, 175, 80, 0.1) 100%);
  position: relative;
  overflow: hidden;
}

.contact-section::before {
  content: '';
  position: absolute;
  top: -30%;
  right: -30%;
  width: 60%;
  height: 60%;
  background: radial-gradient(circle, rgba(100, 181, 246, 0.1) 0%, transparent 70%);
  animation: float 15s ease-in-out infinite reverse;
  z-index: 0;
}

.contact-section .container {
  position: relative;
  z-index: 1;
}

/* === EN-TÊTE DE SECTION === */
.contact-section .section-header {
  text-align: center;
  margin-bottom: var(--space-4xl);
  animation: var(--animation-fade-in);
}

.contact-section .section-header h2 {
  font-size: var(--text-4xl);
  font-weight: var(--font-bold);
  margin-bottom: var(--space-lg);
  background: linear-gradient(135deg, var(--secondary-color), var(--primary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
}

.contact-section .section-header h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, var(--secondary-color), var(--primary-color));
  border-radius: var(--radius-full);
}

.contact-section .section-header p {
  font-size: var(--text-lg);
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* === CONTENU PRINCIPAL === */
.contact-content {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: var(--space-4xl);
  align-items: start;
}

/* === INFORMATIONS DE CONTACT === */
.contact-info {
  display: grid;
  gap: var(--space-xl);
}

.contact-card {
  background: var(--surface-glass);
  border-radius: var(--radius-xl);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: var(--blur-md);
  padding: var(--space-xl);
  transition: var(--transition-spring);
  animation: var(--animation-slide-in);
  position: relative;
  overflow: hidden;
}

.contact-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, var(--secondary-color), var(--primary-color));
  transition: var(--transition-normal);
}

.contact-card:hover {
  transform: translateX(5px) scale(1.02);
  box-shadow: var(--shadow-xl);
  border-color: rgba(100, 181, 246, 0.3);
}

.contact-card:hover::before {
  left: 0;
}

.contact-card {
  display: flex;
  align-items: center;
  gap: var(--space-lg);
}

.contact-icon {
  font-size: var(--text-3xl);
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, var(--secondary-color), var(--primary-color));
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-lg);
  position: relative;
  overflow: hidden;
}

.contact-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.1), transparent);
  animation: float 3s ease-in-out infinite;
}

.contact-details h4 {
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  color: var(--white);
  margin-bottom: var(--space-xs);
}

.contact-details p {
  font-size: var(--text-base);
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: var(--space-xs);
  font-weight: var(--font-medium);
}

.contact-note {
  font-size: var(--text-sm);
  color: rgba(255, 255, 255, 0.6);
  font-style: italic;
}

/* === SECTION FORMULAIRE === */
.contact-form-section {
  background: var(--surface-glass);
  border-radius: var(--radius-2xl);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: var(--blur-lg);
  padding: var(--space-3xl);
  animation: var(--animation-scale-in);
  position: relative;
  overflow: hidden;
}

.contact-form-section::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  opacity: 0.7;
}

.form-container h3 {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--white);
  margin-bottom: var(--space-2xl);
  text-align: center;
}

/* === FORMULAIRE === */
.contact-form {
  display: grid;
  gap: var(--space-xl);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.form-group label {
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  color: rgba(255, 255, 255, 0.9);
}

.form-group input,
.form-group select,
.form-group textarea {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-lg);
  padding: var(--space-md);
  font-size: var(--text-base);
  color: var(--white);
  font-family: var(--font-primary);
  transition: var(--transition-normal);
  backdrop-filter: var(--blur-sm);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--secondary-color);
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 0 3px rgba(100, 181, 246, 0.2);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
  font-family: var(--font-primary);
}

.form-group select {
  cursor: pointer;
}

.form-group select option {
  background: var(--darker-gray);
  color: var(--white);
}

/* Bouton de soumission */
.contact-form .btn-primary {
  background: linear-gradient(135deg, var(--secondary-color), var(--primary-color));
  color: var(--white);
  border: none;
  padding: var(--space-lg) var(--space-2xl);
  border-radius: var(--radius-xl);
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  cursor: pointer;
  transition: var(--transition-spring);
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-lg);
  margin-top: var(--space-lg);
}

.contact-form .btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(255, 255, 255, 0.2), 
    transparent);
  transition: var(--transition-normal);
}

.contact-form .btn-primary:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: var(--shadow-xl);
}

.contact-form .btn-primary:hover::before {
  left: 100%;
}

.contact-form .btn-primary:active {
  transform: translateY(-1px) scale(1.01);
}

/* === SECTION POURQUOI ME CHOISIR === */
.why-choose-me {
  margin-top: var(--space-3xl);
  padding-top: var(--space-2xl);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.why-choose-me h4 {
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
  color: var(--white);
  margin-bottom: var(--space-lg);
  text-align: center;
}

.benefits-list {
  list-style: none;
  display: grid;
  gap: var(--space-md);
}

.benefits-list li {
  padding: var(--space-md);
  background: rgba(255, 255, 255, 0.05);
  border-radius: var(--radius-lg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  font-size: var(--text-base);
  color: rgba(255, 255, 255, 0.9);
  transition: var(--transition-normal);
  cursor: pointer;
}

.benefits-list li:hover {
  background: rgba(100, 181, 246, 0.1);
  border-color: rgba(100, 181, 246, 0.3);
  transform: translateX(5px);
}

/* =====================================
   RESPONSIVE DESIGN - CONTACT
   ===================================== */

@media (max-width: 991px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: var(--space-3xl);
  }
  
  .contact-info {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .contact-section {
    padding: var(--space-3xl) 0;
  }
  
  .contact-section .section-header h2 {
    font-size: var(--text-3xl);
  }
  
  .contact-content {
    gap: var(--space-2xl);
  }
  
  .contact-info {
    grid-template-columns: 1fr;
    gap: var(--space-lg);
  }
  
  .contact-card {
    padding: var(--space-lg);
    flex-direction: column;
    text-align: center;
    gap: var(--space-md);
  }
  
  .contact-icon {
    width: 50px;
    height: 50px;
    font-size: var(--text-2xl);
  }
  
  .contact-form-section {
    padding: var(--space-xl);
  }
  
  .form-container h3 {
    font-size: var(--text-xl);
  }
  
  .contact-form {
    gap: var(--space-lg);
  }
  
  .contact-form .btn-primary {
    padding: var(--space-md) var(--space-xl);
    font-size: var(--text-base);
  }
  
  .why-choose-me {
    margin-top: var(--space-xl);
    padding-top: var(--space-xl);
  }
}

@media (max-width: 480px) {
  .contact-form-section {
    padding: var(--space-lg);
  }
  
  .contact-card {
    padding: var(--space-md);
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: var(--space-sm);
    font-size: var(--text-sm);
  }
  
  .contact-form .btn-primary {
    padding: var(--space-sm) var(--space-lg);
    font-size: var(--text-sm);
  }
}