/* =====================================
   OPTIMISATIONS RESPONSIVE FINALES
   Cohérence parfaite sur tous les écrans
   ===================================== */

/* === BREAKPOINTS ET CONTENEURS === */
@media (min-width: 1400px) {
  .container-fluid {
    max-width: 1600px;
    margin: 0 auto;
  }
}

/* === OPTIMISATIONS POUR ÉCRANS EXTRA LARGES === */
@media (min-width: 1600px) {
  :root {
    --text-5xl: 4rem;    /* 64px */
    --text-4xl: 3rem;    /* 48px */
    --text-3xl: 2.5rem;  /* 40px */
  }
  
  .hero-section {
    min-height: 100vh;
    padding: var(--space-5xl) 0;
  }
  
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: var(--space-3xl);
  }
  
  .project-card.featured {
    grid-column: span 2;
  }
}

/* === OPTIMISATIONS POUR TABLETTES LANDSCAPE === */
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .hero-section .content-wrapper {
    grid-template-columns: 1.2fr 1fr;
    align-items: center;
    gap: var(--space-3xl);
  }
  
  .avatar-container {
    height: 70vh;
    max-height: 500px;
  }
  
  .contact-content {
    grid-template-columns: 1fr 1.1fr;
    gap: var(--space-2xl);
  }
  
  .navigation {
    padding: var(--space-md) var(--space-lg);
  }
  
  .nav-avatar {
    width: 120px;
    height: 120px;
  }
}

/* === OPTIMISATIONS POUR TABLETTES PORTRAIT === */
@media (min-width: 768px) and (max-width: 1024px) and (orientation: portrait) {
  .hero-section .content-wrapper {
    grid-template-columns: 1fr;
    gap: var(--space-2xl);
    text-align: center;
  }
  
  .avatar-container {
    height: 60vh;
    max-height: 400px;
    margin: 0 auto;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: var(--space-xl);
  }
  
  .project-card.featured {
    grid-column: span 1;
  }
  
  .contact-content {
    grid-template-columns: 1fr;
    gap: var(--space-xl);
  }
  
  .contact-info {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--space-lg);
  }
}

/* === OPTIMISATIONS POUR MOBILES LANDSCAPE === */
@media (max-width: 767px) and (orientation: landscape) {
  .hero-section {
    padding: var(--space-2xl) 0;
  }
  
  .hero-section .content-wrapper {
    grid-template-columns: 1.2fr 0.8fr;
    gap: var(--space-lg);
    align-items: center;
  }
  
  .text-content {
    padding-right: var(--space-lg);
  }
  
  .hero-title {
    font-size: var(--text-2xl);
    margin-bottom: var(--space-md);
  }
  
  .hero-subtitle {
    font-size: var(--text-lg);
    margin-bottom: var(--space-sm);
  }
  
  .hero-description {
    font-size: var(--text-sm);
    margin-bottom: var(--space-lg);
    line-height: 1.5;
  }
  
  .hero-stats {
    grid-template-columns: repeat(3, 1fr);
    gap: var(--space-sm);
    margin: var(--space-md) 0;
  }
  
  .stat {
    padding: var(--space-sm);
  }
  
  .stat-number {
    font-size: var(--text-lg);
  }
  
  .stat-label {
    font-size: var(--text-xs);
  }
  
  .avatar-container {
    height: 40vh;
    max-height: 250px;
  }
  
  .navigation {
    padding: var(--space-sm) var(--space-md);
  }
  
  .nav-avatar {
    width: 80px;
    height: 80px;
  }
}

/* === OPTIMISATIONS POUR TRÈS PETITS ÉCRANS === */
@media (max-width: 320px) {
  :root {
    --space-md: 0.75rem;
    --space-lg: 1rem;
    --space-xl: 1.25rem;
    --space-2xl: 1.5rem;
  }
  
  .container {
    padding: 0 var(--space-sm);
  }
  
  .hero-title {
    font-size: var(--text-xl);
  }
  
  .hero-subtitle {
    font-size: var(--text-base);
  }
  
  .hero-description {
    font-size: var(--text-sm);
    line-height: 1.4;
  }
  
  .hero-stats {
    grid-template-columns: 1fr;
    gap: var(--space-sm);
  }
  
  .cta-buttons {
    flex-direction: column;
    gap: var(--space-sm);
  }
  
  .project-card {
    padding: var(--space-md);
  }
  
  .contact-card {
    padding: var(--space-sm);
    flex-direction: column;
    text-align: center;
    gap: var(--space-sm);
  }
  
  .contact-icon {
    width: 40px;
    height: 40px;
    font-size: var(--text-lg);
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: var(--space-sm);
    font-size: var(--text-sm);
  }
  
  .btn {
    padding: var(--space-sm) var(--space-md);
    font-size: var(--text-sm);
  }
}

/* === OPTIMISATIONS POUR RÉDUIRE LES ANIMATIONS SUR MOBILES === */
@media (max-width: 767px) {
  .hover-float:hover,
  .hover-pulse:hover,
  .hover-glow:hover {
    animation: none;
  }
  
  .card:hover {
    transform: none;
  }
  
  .project-card:hover {
    transform: none;
  }
  
  .contact-card:hover {
    transform: none;
  }
  
  .btn:hover {
    transform: none;
  }
  
  .cta-stat:hover {
    transform: translateY(-2px) scale(1.02);
  }
}

/* === OPTIMISATIONS POUR PRÉFÉRENCES D'ACCESSIBILITÉ === */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  
  .loading-spinner {
    animation: none;
    border: 2px solid var(--primary-color);
  }
  
  .progress-enhanced::after {
    animation: none;
  }
}

/* === OPTIMISATIONS POUR THÈME SOMBRE FORCÉ === */
@media (prefers-color-scheme: dark) {
  :root {
    --surface-primary: rgba(255, 255, 255, 0.08);
    --surface-secondary: rgba(138, 132, 226, 0.08);
    --surface-glass: rgba(255, 255, 255, 0.12);
    --surface-glass-hover: rgba(138, 132, 226, 0.15);
  }
}

/* === OPTIMISATIONS POUR CONTRASTE ÉLEVÉ === */
@media (prefers-contrast: high) {
  :root {
    --primary-color: #9D97E8;
    --secondary-color: #70C1F7;
    --surface-glass: rgba(255, 255, 255, 0.2);
    --surface-glass-hover: rgba(138, 132, 226, 0.3);
  }
  
  .card,
  .project-card,
  .contact-card {
    border: 2px solid rgba(255, 255, 255, 0.3);
  }
  
  .btn {
    border: 2px solid currentColor;
  }
  
  .nav-link-enhanced {
    border: 1px solid rgba(255, 255, 255, 0.2);
  }
}

/* === OPTIMISATIONS POUR PERFORMANCE SUR MOBILES === */
@media (max-width: 767px) {
  .projects-section::before,
  .contact-section::before {
    display: none;
  }
  
  .project-card::after,
  .card::before {
    display: none;
  }
  
  .btn::before,
  .tech-tag::before,
  .contact-card::before {
    display: none;
  }
  
  .progress-enhanced::after {
    display: none;
  }
}

/* === PRINT STYLES === */
@media print {
  .navigation,
  .avatar-container,
  .cta-buttons,
  .contact-form-section {
    display: none !important;
  }
  
  .hero-section,
  .projects-section,
  .contact-section {
    background: white !important;
    color: black !important;
    box-shadow: none !important;
  }
  
  .project-card,
  .contact-card {
    background: white !important;
    border: 1px solid #ccc !important;
    break-inside: avoid;
  }
  
  .section-header h2 {
    color: black !important;
    background: none !important;
    -webkit-text-fill-color: black !important;
  }
}

/* === FOCUS POUR ACCESSIBILITÉ === */
.btn:focus,
.nav-link-enhanced:focus,
.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: 3px solid var(--primary-color);
  outline-offset: 2px;
}

/* === SCROLL SMOOTH POUR NAVIGATEURS COMPATIBLES === */
@supports (scroll-behavior: smooth) {
  html {
    scroll-behavior: smooth;
  }
}

/* === BACKDROP FILTER FALLBACK === */
@supports not (backdrop-filter: blur(10px)) {
  .glass,
  .card,
  .project-card,
  .contact-card,
  .nav-enhanced,
  .contact-form-section {
    background: rgba(255, 255, 255, 0.15);
  }
}