/* =====================================
   STYLES POUR LA SECTION PROJETS
   Design moderne avec cards et effets avancés
   ===================================== */

.projects-section {
  padding: var(--space-5xl) 0;
  background: linear-gradient(135deg, 
    rgba(138, 132, 226, 0.1) 0%, 
    rgba(100, 181, 246, 0.1) 50%,
    rgba(76, 175, 80, 0.1) 100%);
  position: relative;
  overflow: hidden;
}

.projects-section::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(138, 132, 226, 0.05) 0%, transparent 70%);
  animation: float 20s ease-in-out infinite;
  z-index: 0;
}

.projects-section .container {
  position: relative;
  z-index: 1;
}

/* === EN-TÊTE DE SECTION === */
.projects-section .section-header {
  text-align: center;
  margin-bottom: var(--space-4xl);
  animation: var(--animation-fade-in);
}

.projects-section .section-header h2 {
  font-size: var(--text-4xl);
  font-weight: var(--font-bold);
  margin-bottom: var(--space-lg);
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
}

.projects-section .section-header h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  border-radius: var(--radius-full);
}

.projects-section .section-header p {
  font-size: var(--text-lg);
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* === GRILLE DES PROJETS === */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--space-2xl);
  margin-bottom: var(--space-4xl);
}

/* === CARTES DE PROJET === */
.project-card {
  background: var(--surface-glass);
  border-radius: var(--radius-2xl);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: var(--blur-lg);
  padding: var(--space-2xl);
  position: relative;
  overflow: hidden;
  transition: var(--transition-spring);
  animation: var(--animation-slide-up);
  cursor: pointer;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color), var(--accent-color));
  opacity: 0;
  transition: var(--transition-normal);
}

.project-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: var(--shadow-2xl);
  border-color: rgba(138, 132, 226, 0.3);
}

.project-card:hover::before {
  opacity: 1;
}

/* Projet Featured */
.project-card.featured {
  grid-column: span 2;
  background: linear-gradient(135deg, 
    rgba(138, 132, 226, 0.15) 0%, 
    rgba(100, 181, 246, 0.1) 100%);
  border: 2px solid rgba(138, 132, 226, 0.3);
  position: relative;
}

.project-card.featured::after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, var(--primary-color), var(--secondary-color), var(--primary-color));
  border-radius: var(--radius-2xl);
  z-index: -1;
  opacity: 0.5;
  animation: glow 3s ease-in-out infinite;
}

/* Badge du projet */
.project-badge {
  display: inline-block;
  background: linear-gradient(135deg, var(--accent-color), #66BB6A);
  color: var(--white);
  font-size: var(--text-sm);
  font-weight: var(--font-semibold);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-full);
  margin-bottom: var(--space-lg);
  box-shadow: var(--shadow-accent);
  animation: var(--animation-bounce-in);
}

/* Contenu du projet */
.project-content h3 {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  margin-bottom: var(--space-lg);
  color: var(--white);
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.project-content p {
  font-size: var(--text-base);
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.85);
  margin-bottom: var(--space-xl);
}

/* === FONCTIONNALITÉS DU PROJET === */
.project-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
}

.feature {
  background: rgba(255, 255, 255, 0.1);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-lg);
  font-size: var(--text-sm);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: var(--transition-normal);
  text-align: center;
}

.feature:hover {
  background: rgba(138, 132, 226, 0.2);
  border-color: rgba(138, 132, 226, 0.4);
  transform: translateY(-2px);
}

/* === TECHNOLOGIES === */
.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-sm);
  margin-bottom: var(--space-xl);
}

.tech-tag {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: var(--white);
  font-size: var(--text-xs);
  font-weight: var(--font-medium);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-full);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.tech-tag::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: var(--transition-normal);
}

.tech-tag:hover::before {
  left: 100%;
}

.tech-tag:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-primary);
}

/* === MÉTRIQUES DU PROJET === */
.project-metrics {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: var(--space-lg);
  margin-top: var(--space-xl);
  padding-top: var(--space-xl);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.metric {
  text-align: center;
}

.metric-number {
  display: block;
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--secondary-color);
  margin-bottom: var(--space-xs);
}

.metric-label {
  font-size: var(--text-sm);
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.4;
}

/* === CALL TO ACTION === */
.projects-cta {
  text-align: center;
  background: var(--surface-glass);
  border-radius: var(--radius-2xl);
  padding: var(--space-3xl);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: var(--blur-md);
  animation: var(--animation-scale-in);
}

.projects-cta p {
  font-size: var(--text-xl);
  margin-bottom: var(--space-lg);
  color: rgba(255, 255, 255, 0.9);
}

.projects-cta .btn-primary {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: var(--white);
  border: none;
  padding: var(--space-lg) var(--space-2xl);
  border-radius: var(--radius-xl);
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  cursor: pointer;
  transition: var(--transition-spring);
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-lg);
}

.projects-cta .btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(255, 255, 255, 0.2), 
    transparent);
  transition: var(--transition-normal);
}

.projects-cta .btn-primary:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: var(--shadow-xl);
}

.projects-cta .btn-primary:hover::before {
  left: 100%;
}

.projects-cta .btn-primary:active {
  transform: translateY(-1px) scale(1.02);
}

/* =====================================
   RESPONSIVE DESIGN - PROJETS
   ===================================== */

@media (max-width: 991px) {
  .project-card.featured {
    grid-column: span 1;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: var(--space-xl);
  }
}

@media (max-width: 767px) {
  .projects-section {
    padding: var(--space-3xl) 0;
  }
  
  .projects-section .section-header h2 {
    font-size: var(--text-3xl);
  }
  
  .project-card {
    padding: var(--space-xl);
  }
  
  .project-content h3 {
    font-size: var(--text-xl);
  }
  
  .project-features {
    grid-template-columns: 1fr;
  }
  
  .project-metrics {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .projects-cta {
    padding: var(--space-xl);
  }
  
  .projects-cta p {
    font-size: var(--text-lg);
  }
  
  .projects-cta .btn-primary {
    padding: var(--space-md) var(--space-xl);
    font-size: var(--text-base);
  }
}

@media (max-width: 480px) {
  .projects-grid {
    grid-template-columns: 1fr;
    gap: var(--space-lg);
  }
  
  .project-card {
    padding: var(--space-lg);
  }
  
  .project-metrics {
    grid-template-columns: 1fr;
    gap: var(--space-md);
  }
  
  .tech-tag {
    font-size: var(--text-xs);
    padding: var(--space-xs) var(--space-sm);
  }
}